.backdrop{
    width:100%;
    height:100%; 
    position:fixed; 
    top:0px; 
    left:0px; 
    background-color: rgba(0, 0, 0, 0.5);
}

.modal-img{ 
    width:80%; 
    height: 80%;
    background-size:cover; 
    background-position:center; 
    margin:auto;
    margin-top:8%; 
    border:3px solid white; 
    box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
    z-index:10;
}
:root{
    --primary:#f84848;
}

*{
    margin:0px; 
    padding:0px; 
    box-sizing:border-box;

}

.Popup-Card-container{
    min-height:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:10px 10px; 
}

.Popup-Card-subcontainer{
    width:860px; 
    display:flex; 
    justify-content: center;
    align-items: center;
    
}

.ProductBackground{
    width:50%; 
    height:475px; 
    position:relative;
    background-size: cover !important;
    background-position:center !important;
    box-shadow: -15px 0 35px rgba(0,0,0,0.1),0 -15px 35px rgba(0,0,0,0.1);
}

.gradients{
    position: absolute;
    width: 100%;
    height: 100%;
    top:0px; 
    left:0px; 

}

.gradient{
    position: absolute;
    width: 100%;
    height: 100%;
    top:0px; 
    left:0px; 
    z-index:-2;
}

.first{
    z-index:0;
    -webkit-animation: 1.3s width ease-in-out;
            animation: 1.3s width ease-in-out;
}

.second{
    z-index:-1; 
}

@-webkit-keyframes width{
    from{width:0%}
    to{width:100%}
}

@keyframes width{
    from{width:0%}
    to{width:100%}
}

.gradient[color='blue']{
    background-image:linear-gradient(45deg,#0136af,#22abfa);
}

.gradient[color='red']{
    background-image:linear-gradient(45deg,#d62926,#ee625f);
}

.gradient[color='green']{
    background-image:linear-gradient(45deg,#11998e,#1ce669)
}

.gradient[color='orange']{
    background-image:linear-gradient(45deg,#fc4a1a,#f7b733)
}

.gradient[color='black']{
    background-image:linear-gradient(45deg,#111,#666)
}

.logo{
    width:100px;
    position:absolute; 
    top:20px; 
    left:20px; 
}

.share{
    width:50px; 
    height:50px; 
    position:absolute;
    top:15px; 
    right:15px; 
    background-color: white;
    border-radius: 50%; 
    transition:0.2s; 
