*{
    margin:0px; 
    padding:0px; 
    box-sizing: border-box;
}
.card{
    width:300px; 
    height:450px; 
    position: relative;
    background-color: white;
    display:grid;
    grid-template-rows:auto 40%; 
    overflow: hidden;
    margin:auto; 
    margin-bottom:25px; 
}

#card-image-container{
    width:100%;
    position:relative;
   
}

#card-img{
    height:100%; 
    position: absolute;
    left:50%; 
    -webkit-transform:translateX(-50%); 
            transform:translateX(-50%);
    transition:0.7s; 
}

#card-bottom{
    display: grid;
    justify-content: center;
    align-content: center;
    text-align: center;
    position:relative; 
}

#card-btn{
    display:none;
    width: 42px;
    height:42px;
    position: absolute;
    left:50%; 
    margin-right:50%; 
    -webkit-transform: translate(-50%,-50%); 
            transform: translate(-50%,-50%);
    padding-top: 6px;
    background-color: orangered;
    border:none; 
    cursor:pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border-radius: 50%; 
    transition: 0.7s;
    outline:none; 
}

#btn-text{
    height:100%;
    color:white; 
    display:grid;
    grid-template-rows: 1fr 1fr;
    transition: 0.7s;

}

#btn-text span{
    font-size: 12px;
    font-weight: bold;
    margin:5px;
    margin-top: 9px;
}

#card-texts{
    width:100%; 
    padding:0px 40px; 
    display:flex; 
    flex-direction:column; 
    position:absolute;
    top:16.5px;  
}

#title-badge{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 6px;
    border-bottom: 1px solid #dadada;
    padding-bottom:8px; 
}

#card-title{
    margin-right: 15px;
