    transition: 0.7s;
    margin-top:3px;
    color:#555;  
    font-size:16px; 
}

#badge{
    width:55px;
    height:30px; 
    font-size:12px; 
    font-weight:bold; 
    padding:6px 10px; 
    background-color: #555;
    color:white; 
    border-radius:8px;  
}

#card-paragraph{
    height:52px; 
    margin-top:2px; 
    z-index:1;
    transition: 0.7s;
    color:#555; 
    font-size:14px; 
}

#social{
    width:100%; 
    height: 100%;
    position:absolute;
    top:-100%; 
    left:0px; 
    color:white; 
    background-color:transparent; 
    display:flex; 
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index:0;
    transition:0.7s; 
}

#social p{
    padding:10px 10px;
    border:1px solid white; 
    border-radius: 3px;
    -webkit-user-select:none;
        -ms-user-select:none;
            user-select:none;
    cursor:pointer; 
}

#social-links{
    width:100%; 
    margin-top:20px;
    padding:0px 45px; 
    display:flex;
    justify-content: space-between; 
    align-items: center;
}

#social-links a{
