    height: fit-content; /* because will span the whole height if this is not set*/
}

#social-links img{
    width:50px; 
    height: 50px;

}

#email{
    width:100px; 
    height:40px;
    margin-top:20px;
    text-decoration: none;
    padding:10px 10px; 
    color:white; 
    border:1px solid white;
    display:flex;
    justify-content: center;
}

.card.active #card-title, .card.active #card-paragraph{
    -webkit-transform: translateY(-300px);
            transform: translateY(-300px);
    color:white;
}

.card.active #card-btn{
    -webkit-transform: translate(-50%,45px);
            transform: translate(-50%,45px);

}

.card.active #email{
    width:200px; 
    border-radius: 7px;
    transition:0.2s; 
}

.card.active #social-links a{
    transition: 0.2s;
}

.card.active #social-links a:hover{
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
}

.card.active #card-img{
    height:450px; 
    

}

.card.active #btn-text{
    -webkit-transform: translateY(-26px);
            transform: translateY(-26px);
}

.card.active #social{
    top:0px; 
